import React from 'react';
import './App.css';
import Loadable from "react-loadable";
import { BrowserRouter, Switch, Route } from "react-router-dom";


function App() {

  const loading = (
    <div style={{display:"flex",flexDirection:"column" ,justifyContent:"center",alignItems:"center"}}>
    <img src="https://media.giphy.com/media/MZ9cQ7JpR7iDIlSUvm/giphy.gif" style={{width:"30%"}}/>
    <div style={{ fontSize:"40px",color:"#189AB4",fontWeight:"bold" }}>Loading...</div>
 </div>
  );

  const Home = Loadable({
    loader: () => import("./Pages/Home/index"),
    loading: () => loading,
  });
  const Staff = Loadable({
    loader: () => import("./Pages/Staff/index"),
    loading: () => loading,
  });
  const StudentResult = Loadable({
    loader: () => import("./Pages/StudentResult/index"),
    loading: () => loading,
  });

  return(
    <BrowserRouter>
      <Switch>
        <Route exact path="/" component={Home} />
        <Route exact path="/faculty" component={Staff} />
        <Route exact path="/studentresult" component={StudentResult} />
      </Switch>
    </BrowserRouter>
  )
}
export default App;
